<template>
	<div class="login-container">
		<van-tabs type="card" v-model="loginTabActive" @click="clickLoginTab">
			<van-tab title="登录">
				<van-form style="margin-top: 5vw;">
					<van-field v-model="email" name="邮箱" label="邮箱" placeholder="邮箱" />
					<van-field v-model="pwd" type="password" name="密码" label="密码" placeholder="密码" />
					<div style="margin: 16px;">
						<van-button color="#ee0a24" round block type="info" @click="formSubmit">
							登录
						</van-button>
					</div>
				</van-form>
			</van-tab>
			<van-tab title="注册">
				<van-form style="margin-top: 5vw;">
					<van-field v-model="email" name="邮箱" label="邮箱" placeholder="邮箱">
						<van-button slot="button" size="small" :disabled="!!codeSend" type="default" @click="handleCodeSend" native-type="button">{{codeText}}</van-button>
					</van-field>
					<van-field v-model="code" name="邮箱验证码" label="邮箱验证码" placeholder="邮箱验证码" />
					<van-field v-model="nick" name="昵称" label="昵称" placeholder="昵称" />
					<van-field v-model="pwd" type="password" name="密码" label="密码" placeholder="密码" />
					<div style="margin: 16px;">
						<van-button color="#ee0a24" round block type="info" @click="formSubmit">
							注册
						</van-button>
					</div>
				</van-form>
			</van-tab>
		</van-tabs>
	</div>
</template>

<script>
	import {
		isEmail,
		isEmpty
	} from '@/common/validate/validate'
	import util from '@/common/util/util'
	import random from '@/common/random/random.js'
	var codeTimer;
	export default {
		data() {
			return {
				loginTabActive: 0,
				email: '',
				nick: '',
				pwd: '',
				code: '',
				codeSend: 0, //0获取验证,1验证码倒计时
				codeText: '获取验证码',
				hasLogin: false
			};
		},
		created() {
			// console.log(randomNumber());
		},
		methods: {
			clickLoginTab(name, title) {
				//name 编号
				//title 标题
				console.log(name, title);
			},
			formSubmit() {
				if (!isEmail(this.email)) {
					this.$toast('邮箱格式错误');
					return;
				}
				// 密码123456
				if (isEmpty(this.pwd)) {
					this.$toast('请输入正确的密码');
					return;
				}
				// 用户名
				if (this.loginTabActive && isEmpty(this.nick)) {
					this.$toast('请输入昵称');
					return;
				}
				// 邮箱验证码4位数字
				if (this.loginTabActive && !/^\d{4}$/.test(this.code)) {
					this.$toast('请输入正确的验证码');
					return;
				}
				// 登录中
				this.$toast.loading({
					mask: true,
					message: '登录中...',
					duration: 0
				})
				// 注册&登录...
				this.loginTabActive ? this.emailRegister() : this.emailLogin()
			},
			emailRegister() {
				const params = {
					code: this.code,
					email: this.email,
					pwd: this.pwd,
					nick: this.nick || `用户${Math.random()(10000)+1}`,
					autoLogin: true // 该参数可以自动完成登录并返回token
				}
				this.$request.post('/user/email/register', params).then(res => {
					if (res.code !== 0) {
						this.$toast(res.msg)
						return
					}
					// this.mobileLogin()
					util.storage.set('token', res.data.token);
					util.storage.set('uid', res.data.uid);
					this.$route.query.redirect ? this.$router.replace({
						path: this.$route.query.redirect
					}) : this.$router.replace({
						path: '/home'
					})
					// this.$toast.clear()
					this.$toast.success({
						message: '注册成功',
						duration: 1500
					})
				})
			},
			emailLogin() {
				// 固定为0529，小米note3
				const params = {
					deviceId: '114514',
					deviceName: '安卓',
					email: this.email,
					pwd: this.pwd
				}
				this.$request.post('/user/email/login', params).then(res => {
					if (res.code !== 0) {
						this.$toast(res.msg)
						return
					}
					util.storage.set('token', res.data.token)
					util.storage.set('uid', res.data.uid)
					this.$route.query.redirect ? this.$router.replace({
						path: this.$route.query.redirect
					}) : this.$router.replace({
						path: '/home'
					})
					// this.$toast.clear()
					this.$toast.success({
						message: '登录成功',
						duration: 1500
					})
				})
			},
			handleCodeSend() {
				//发送验证码
				if (!isEmail(this.email)) {
					this.$toast('邮箱格式错误');
					return;
				}
				if (this.codeSend === 1) {
					return;
				}
				clearInterval(codeTimer);
				let time = 60;
				codeTimer = setInterval(() => {
					if (time <= 0) {
						this.codeSend = 0;
						this.codeText = '获取验证码'
					} else {
						this.codeSend = 1
						this.codeText = `已发送(${time}s)`
						time--;
					}
				}, 1000);
				this.$request.get('/verification/mail/get', {
					mail: this.email
				}).then(res => {
					if (res.code === 5001) {
						this.$toast(res.msg);
					} else {
						this.$toast('验证码发送成功');
						// console.log(`/verification/mail/get:${JSON.stringify(res)}`)
					}

				})
			}
		}
	};
</script>

<style scoped>
	.login-container {
		margin-top: 30vw;
		/* padding-top: 60px;
		box-sizing: border-box;
		display: flex;
		flex-direction:column;
		align-items: center; 
		justify-content: center; */
	}
</style>
